body {
    font-size: 25px;
}
.delete {
   font-family: 'Poppins', sans-serif;
   width: 100px;
   height: 38px;
   padding: 2px;
   background-color: #b02323;
   border: 2px solid #ddd;
   border-radius: 5px;
   transition-duration: 0.1s;
   color: #dee0de;
   font-family: 'Poppins', sans-serif;
   transition-duration: 0.1s;
   font-size: 20px;
}

.delete:hover {
    background-color: #941c1c;
}

.delete:active {
    transform: scale(0.98);
}

.checkbox {
    width: 40px;
    height: 32px;
    position: relative;
    top: 9.5px;
}