
body {
    background-color: #ebebeb;
    font-family: 'Poppins', sans-serif;
}


.container {
    font-family: 'Poppins', sans-serif;
    background-color: #e0e0e0;
    max-width: 650px;
    margin: 0 auto;
    margin-top: 25px;
    border-radius: 5px;
    padding: 8px;

    -webkit-box-shadow: 18px 9px 30px -1px rgba(66, 68, 90, 1);
    -moz-box-shadow: 18px 9px 30px -1px rgba(66, 68, 90, 1);
    box-shadow: 18px 9px 30px -1px rgba(66, 68, 90, 1);
}


.App {
    margin-top: 12px;
    align-content: center;
    align-items: center;
    text-align: center;
}

.addTodoBtn {
   margin-top: 6px;
   width: 250px;
   height: 40px;
   padding: 3px;
   background-color: #1da346;
   border: 2px solid #ddd;
   border-radius: 8px;
   transition-duration: 0.1s;
   color: #dee0de;
   font-family: 'Poppins', sans-serif;
   font-size: 20px;
}

.addTodoBtn:hover {
    background-color: #2f9c34;
}

.addTodoBtn:active {
    background-color: #2f9c34;
    transform: scale(0.98)
}

.todoInput {
    margin-top: 15px;
    border: 2px solid #ddd;
    width: 500px;
    height: 35px;
    border-radius: 6px;
    padding: 5px;
    font-size: 20px;
}

.clearTodo {
    margin-top: 15px;
    width: 250px;
    height: 40px;
    padding: 3px;
    background-color: #1d57a3; 
    border: 2px solid #ddd;
    border-radius: 8px;
    transition-duration: 0.1s;
    color: #dee0de;
    font-family: 'Poppins', sans-serif;
    font-size: 20px;
 }
 
 .clearTodo:hover {
     background-color: #164380;
 }
 
 .clearTodo:active {
     background-color: #164380;
     transform: scale(0.98)
 }

 .toDoLeft {
     margin-top: 10px;
 }